import { HttpRequest, HttpResponse } from '@angular/common/http';
import { Observable } from 'rxjs';

export abstract class BaseMockedApi {
  static get path(): string {
    return '';
  }
  static get baseUrl(): string | undefined {
    return undefined;
  }
  static get method(): string {
    return 'get';
  }
  static get isAuthCheck(): boolean {
    return true;
  }
  abstract handle(request: HttpRequest<any>): Observable<HttpResponse<any>>;
}
