import { Router } from '@angular/router';
import { Component, EventEmitter, OnDestroy, OnInit, Output,Renderer2, ViewChild } from '@angular/core';
import { MatMenuTrigger } from '@angular/material/menu';
import { Subscription } from 'rxjs';
import { AuthService } from 'src/app/auth/auth.service';
import { RoutesList } from 'src/app/routes'
import { MessageService } from 'src/app/service/message.service';
import { HeaderService } from '../header/header.service';

@Component({
  selector: 'app-sidbar',
  templateUrl: './sidbar.component.html',
  styleUrls: ['./sidbar.component.scss']
})
export class SidbarComponent implements OnInit, OnDestroy {
  enteredButton = false;
  isMatMenuOpen = false;
  isMatMenu2Open = false;
  leftP:any;
  topP:any;
  languageFlag = 'n1';
  myStyle:object={
    'position': 'absolute',
            'top': '20px!important',
            'left':'0',
            'min-height': '40px!important'
  };
  prevButtonTrigger;
  routes = RoutesList
  sub: Subscription = new Subscription()
  sideOpen
  sideStatus: string = "full";
  activeClass=true;
  navStyle: object = {
    'width': '200px'
  }
  sideToggle
  dir
  deviseWidth
  onSideHover: boolean = false;
  @Output() sideStatusEmit: EventEmitter<string> = new EventEmitter<string>()
  @ViewChild(MatMenuTrigger) trigger: MatMenuTrigger;
  maxHeight = document.body.clientHeight - 83
  role = '';


  constructor(
    private message: MessageService,
    private ren: Renderer2,
    private headerService: HeaderService,
    private auth:AuthService,
    private router:Router
  ) { }

  ngOnInit(): void {
    console.log(localStorage.getItem('role_vr'))
    this.role = localStorage.getItem('role_vr')

    setTimeout(() => {





      // this.routes =  [
      //   {
      //       title: "Dashboard",
      //       mIcon: "dashboard",
      //       id: 'm1',
      //       path: 'dashboard',
      //       index:1,
      //       child: [
      //           { path: 'a1', id: 'm1-1', title: 'Dashboard', mIcon: 'card_travel' ,index:1  },

      //       ],

      //   },
      //   {
      //       title: "Shop",
      //       mIcon: 'shop',
      //       path: 'shop',
      //       index:2,
      //       id:"m2",
      //       child: [
      //           { path: 'b1', title: 'Shop', mIcon: 'shop' , index:1 ,id:'m2-1',child:[]},
      //           { path: 'b2', title: 'Shop Bog', mIcon: 'shopping_bag',index:1,id:'m2-2',child:[] }

      //       ],

      //   }
      // ]

    }, 3000);

    document.addEventListener('click', (e: any) => {
      const el = e.path?.find(fl => fl.id === 'side-toggle' || fl.id === 'nav')
      if (this.sideOpen && this.sideStatus !== 'compact' && this.deviseWidth < 900 && !this.onSideHover && !el) {
        this.sideOpen = !this.sideOpen
        document.getElementById('out-side').classList.remove('toggle-on')
        this.message.sidenaveClose.next(this.sideOpen)
      }
    })
    this.sub.add(
      this.message.sidenaveToggle.subscribe(data => {
        console.log(data)
        if (this.sideStatus === 'static') {
          console.log("static")
          return
        }
        else {
          this.sideToggle = true
          this.sideOpen = data
          const root = document.getElementById('body').getAttribute('dir')
          this.dir = root
          this.deviseWidth = window.innerWidth
          if (this.deviseWidth < 900 && data && this.sideStatus !== 'compact') {
            document.getElementById('out-side').classList.add('toggle-on')
          }
          else {
            document.getElementById('out-side')?.classList?.remove('toggle-on')
          }

        }
      })
    )
    this.sub.add(
      this.message.sidenaveStatus.subscribe(
        data => {
          console.log(data)

          this.sideStatus = data
          if (data === 'mini') {
          console.log('mini')

            this.navStyle = {
              'width': '80px'
            }
          }
          else if (data === 'full') {
          console.log('full')

            this.navStyle = {
              'width': '200px'
            }
          }
          else if (data === 'compact') {
          console.log('compact')

            this.navStyle = {
              'max-width': '50px',
            }
          }
          else if(data === 'static'){
            console.log("ssss")
           this.myStyle={
            'position': 'absolute',
            'top': '-5px',
            'left':'0',
            'min-height': '40px!important'
           }
          }
        }
      )
    )
  }
  ngOnDestroy(): void {
    if (this.sub)
      this.sub.unsubscribe
  }
  getRoutes() {

  }
  activeItem(id) {
    const li = document.getElementById(id)
    let find: boolean = false;
    li.classList.forEach(item => {
      if (item === 'nav-item-active') {
        find = true
        li.classList.remove('nav-item-active')
      }
    })
    if (!find) {
      li.classList.add('nav-item-active')
    }

  }
  sideLayoutToggle() {
    if (this.sideStatus === 'full') {
      this.sideStatus = 'mini';
      this.navStyle = { 'width': '80px' }
    }
    else {
      this.sideStatus = 'full'
      this.navStyle = { 'width': '200px' }
    }
    this.sideStatusEmit.emit(this.sideStatus)
    this.message.sidenaveStatus.next(this.sideStatus)
  }
  logOut(){
    // this.headerService.logOut().subscribe(result=>{
      this.auth.removeLocalStorage('token_VREDU');
      localStorage.removeItem('token_VREDU');
      this.router.navigate(['login']);
    // })
  }
}


