import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable, of, throwError} from 'rxjs';
import { catchError, retry } from 'rxjs/operators';
import {environment} from "../../../../environments/environment";

export const API = `${environment.API_BASE}`;
const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json'
  }),
  // withCredentials: true
};
@Injectable({
  providedIn: 'root'
})
export class CoursesService {

  constructor(private http:HttpClient) { }

  categories(){
    return this.http.get(`${API}/vredu/course/categories`)
      .pipe(catchError(handleError<any>('list categories')));
  }

  getCourses(status?:string,name?:string,category?:string){
    let _url = `${API}/vredu/course`;
    if(status){
      _url = `?status=${status}`;
    }
    if(name){
      if(status || category){
      _url += `&name=${name}`;

      }
      else{
        _url += `?name=${name}`;

      }

    }
    if(category){
      if(name || status){
        _url += `&category=${category}`;

      }
      else{
        _url += `?category=${category}`;

      }

    }
    return this.http.get(_url)
      .pipe(catchError(handleError<any>('list courses')));
  }

  changeBasket(op,id){
    return this.http.get(`${API}/vredu/basket/${op}/${id}`)
    .pipe(catchError(handleError<any>('list categories')));
  }

}
function handleError<T>(operation = 'operation', result?: T) {
  return (error: any): Observable<T> => {

    // TODO: send the error to remote logging infrastructure
    console.error(error); // log to console instead

    // TODO: better job of transforming error for user consumption
    // UtilitesService.log(`${operation} failed: ${error.message}`);

    return throwError(error.error);
  };
}
