  isValidToken(): Promise<boolean> {

      let promise = new Promise<boolean>((resolve, reject) => {
        try {
          return this.http
            .post(
              `${environment.API_BASE}/auth/v1/users/isValidToken`,
              null
            )
            .subscribe((data: any) => {
              var result = data.message === 'OK' ? true : false;
              resolve(result);
            });
        } catch (error) {
          resolve(false);
          this.router.navigate(['/login']);
        }
      });



    return promise;



  }
  generateToken(){
    let refreshToken = this.token.refreshToken
    try {
       this.http
        .post(
          `${environment.API_BASE}/auth/v1/users/generateToken`,{refreshToken}
        )
        .subscribe((data: any) => {
          if(data.message=='OK'){
            this.saveLoaclStorage(data.data)

          }
          // console.log(data)

        });
    } catch (error) {

      this.router.navigate(['/login']);
    }
  }
