import { PageService } from './../pages/page.service';
import { Router } from '@angular/router';
import { Injectable, Injector } from '@angular/core';
import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpInterceptor,
  HttpResponse,
  HttpErrorResponse,
} from '@angular/common/http';
import { Observable } from 'rxjs';
import { map, catchError } from 'rxjs/operators';
import { MessageService } from '../service/message.service';
import { generatID } from '../service/idGn';
import { AuthService } from './auth.service';
import { error } from 'protractor';
import { JwtHelperService } from '@auth0/angular-jwt';
import { strict } from 'assert';
import { stringify } from 'querystring';

@Injectable()
export class HttpConfigInterceptor implements HttpInterceptor {
  auth: AuthService;
  constructor(
    private message: MessageService,
    private inject: Injector,
    private router: Router,
    private servicesMessage: PageService
  ) {
    this.auth = inject.get(AuthService);
  }
  helper = new JwtHelperService();
  intercept(
    request: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    const loading = {
      loading: true,
      text: 'Loading Http Request',
      id: generatID(15),
    };
    this.message.loading.next(loading);
    // request = request.clone({
    //   headers: request.headers.set('Content-Type', 'application/json'),
    // });
    request = request.clone({
      headers: request.headers.set('Accept', 'application/json'),
    });
    request = request.clone({
      setHeaders: {
        Authorization: `Bearer ${this.auth.getLoaclStorage('token_VREDU')}`,
      },
    });
    let decodedToken = JSON.parse(this.auth.getLoaclStorage('token_VREDU'));
    // let decodedToken = this.helper.decodeToken(
    //   this.auth.getLoaclStorage('token_VREDU')
    // );
    console.log(decodedToken);
    if (decodedToken) {
      let info = {
        appId: decodedToken.appId,
        exp: decodedToken.exp,
        iat: decodedToken.iat,
        id: decodedToken.id,
        iss: decodedToken.iss,
        type: decodedToken.type,
        zoneId: decodedToken.zoneId,
        role:decodedToken?.role
      };
      localStorage.setItem('user', JSON.stringify(info));
    }



    // @ts-ignore
    return next.handle(request).pipe(
      map(
        (event: HttpEvent<any>) => {
          if (event instanceof HttpResponse) {
            const data = this.message.loading.value;
            console.log('work itercepter');

            if (data.id === loading.id)
              this.message.loading.next({
                loading: false,
                text: '',
                id: generatID(15),
              });
          }
          return event;
        },
        (er) => {
          this.message.loading.next({
            loading: false,
            text: '',
            id: generatID(15),
          });
          switch (er.status) {
            case 400:
              console.log('error 400:bad request');
              break;
            case 401:
            //send  refresh token

              console.log('error 401: Unauthorized ');
              this.router.navigate(['/login']);
            localStorage.removeItem('token_VREDU');

              break;
            case 402:
              console.log('error 402: Payment Required');
              break;
            case 403:
              console.log('error 403: Forbidden');
              this.router.navigate(['/login']);
            localStorage.removeItem('token_VREDU');
              break;
            case 404:
              console.log('error 404: Not Found');
              break;
            case 405:
              console.log('error 405: Method Not Allowed');
              break;
            case 406:
              console.log('error 405:  Not Acceptable');
              break;
            case 500:
              console.log('error 500:   Internal Server Error');
              break;
            case 501:
              console.log('error 501:    Not Implemented');
              break;
            case 502:
              console.log('error 502:    Bad Gateway');
              break;
            case 503:
              console.log('error 503:    Service Unavailable');
              break;
            case 504:
              console.log('error 504:     Gateway Timeout');
              break;
            default:
              console.log('Time out :(');
              this.router.navigate(['/login']);
              break;
          }
        }
      ),
      catchError((err: HttpErrorResponse): any => {
        this.message.loading.next({
          loading: false,
          text: '',
          id: generatID(15),
        });
        this.servicesMessage.e(err.error.message);
        switch (err.status) {
          case 400:
            console.log('error 400:bad request');
            break;
          case 401:
            console.log('error 401: Unauthorized ');
            this.router.navigate(['/login']);
            localStorage.removeItem('token_VREDU');
            break;
          case 402:
            console.log('error 402: Payment Required');
            break;
          case 403:
            console.log('error 403: Forbidden');
            this.router.navigate(['/login']);
            localStorage.removeItem('token_VREDU');

            break;
          case 404:
            console.log('error 404: Not Found');
            break;
          case 405:
            console.log('error 405: Method Not Allowed');
            break;
          case 406:
            console.log('error 405:  Not Acceptable');
            break;
          case 500:
            console.log('error 500:   Internal Server Error');
            break;
          case 501:
            console.log('error 501:    Not Implemented');
            break;
          case 502:
            console.log('error 502:    Bad Gateway');
            break;
          case 503:
            console.log('error 503:    Service Unavailable');
            break;
          case 504:
            console.log('error 504:     Gateway Timeout');
            break;
          default:
            console.log('Time out :(');
            console.log(err);
            // this.router.navigate(['/login']);
            break;
        }
        return [err];
      })
    );
  }
}
