import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable, of, throwError} from 'rxjs';
import { catchError, retry } from 'rxjs/operators';
import {environment} from "../../../environments/environment";

export const API = `${environment.API_BASE}`;
const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json'
  }),
  // withCredentials: true
};
@Injectable({
  providedIn: 'root'
})
export class LoginService {

  constructor(private http:HttpClient) { }

  Login(zoneAccess:string,password:string,username:string){
    return this.http.post(`${API}/vredu/account/login`, {password,username})
      .pipe(catchError(handleError<any>('login User')));
  }
  isValid(){
    return this.http.post(`${API}/auth/v1/users/isValidToken`, '')
      .pipe(catchError(handleError<any>('validToken')));
  }
  register(data){
    return this.http.post(`${API}/vredu/account/register`, data)
      .pipe(catchError(handleError<any>('Register User')));
  }

}
function handleError<T>(operation = 'operation', result?: T) {
  return (error: any): Observable<T> => {

    // TODO: send the error to remote logging infrastructure
    console.error(error); // log to console instead

    // TODO: better job of transforming error for user consumption
    // UtilitesService.log(`${operation} failed: ${error.message}`);

    return throwError(error.error);
  };
}
