import { Directive, Input, OnInit } from '@angular/core';

@Directive({
  selector: '[WindowToggle]'
})
export class WindowToggleDirective implements OnInit {

  @Input() elId: string
  @Input() toggleId: string
  @Input() className: string
  constructor() { }
  ngOnInit() {
    const popup = document.getElementById(this.elId)
    const toggle = document.getElementById(this.toggleId)

    window.addEventListener('click', (e: any) => {
      const findEl = e.path?.find(el => el === popup || el === toggle)
      if (!findEl) {
        try {
          popup.classList.remove(this.className)

        }
        catch (e) {
        }
      }

    })
  }
}
