import { Injectable, NgZone } from '@angular/core';
import { Observable, Subject } from 'rxjs';
@Injectable({
    providedIn: 'root'
})
export class RespanciveDevice {
    public mobile: boolean = false;
    public tablet: boolean = false;
    public desktop: boolean = false;
    screen = new Subject<string>()
    constructor(
        private zone: NgZone,) {
        window.onresize = (e) => {
            setTimeout(() => {
                this.zoneDevice()
            }, 50);

        };
    }
    zoneDevice() {
        this.zone.run(() => {
            let width = window.innerWidth
            if (!this.mobile && width <= 600) {
                this.mobile = true
                this.tablet = false;
                this.desktop = false;
                this.screen.next('mobile')


            }
            else if (!this.tablet && width >= 601 && width <= 959) {
                this.mobile = false;
                this.tablet = true;
                this.desktop = false;
                this.screen.next('tablet')

            }
            else if (!this.desktop && width >= 960) {
                this.mobile = false;
                this.tablet = false;
                this.desktop = true;
                this.screen.next('desktop')

            }
        });
    }
    loadDevice(): string {
        let width = window.innerWidth
        this.mobile = false
        this.tablet = false;
        this.desktop = false;

        if (!this.mobile && width <= 600) {
            this.mobile = true
            this.tablet = false;
            this.desktop = false;
            return 'mobile'


        }
        else if (!this.tablet && width >= 601 && width <= 959) {
            this.mobile = false;
            this.tablet = true;
            this.desktop = false;
            return 'tablet'

        }
        else if (!this.desktop && width >= 960) {
            this.mobile = false;
            this.tablet = false;
            this.desktop = true;
            return 'desktop'

        }
    }
    getMedia(): Observable<string> {
        return this.screen.asObservable()
    }
}