import { HttpRequest, HttpResponse } from '@angular/common/http';
import { BaseMockedApi } from '../base-mocked-api';
import { Observable, of } from 'rxjs';
import { MOCKED } from '../const';

export class login extends BaseMockedApi {
  //@ts-ignore
  static get method(): string {
    return 'post';
  }
  //@ts-ignore
  static get path(): string {
    return '/vredu/account/login';
  }
  //@ts-ignore
  static get isAuthCheck(): boolean {
    return false;
  }

  handle(request: HttpRequest<any>): Observable<HttpResponse<any>> {
    const user = MOCKED.users.find(
      (i) =>
        i.username === request.body.username &&
        i.password === request.body.password
    );
    if (user) {
      return of(
        new HttpResponse({
          status: 200,
          body: {
            message: 'logged in',
            data: {
              refreshToken: MOCKED.manager_mocked_token,
              accessToken: MOCKED.manager_mocked_token,
            },
          },
        })
      );
    }
    return of(
      new HttpResponse({
        status: 404,
        body: { status: 404, message: 'Account NotFound' },
      })
    );
  }
}
