import { Injectable } from '@angular/core';
import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpInterceptor,
  HttpResponse,
} from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { login } from './mocked/auth/login';
// import { logout } from './mocked/auth/logout';
import { BaseMockedApi } from './mocked/base-mocked-api';
import { MOCKED } from './mocked/const';
import { CourseList } from './mocked/course/course-list';
import { AccountList } from './mocked/account/list';
import { CourseCategoryList } from './mocked/course/category-list';
import { BasketFinancial } from './mocked/basket/financial';
import { BasketWithdraw } from './mocked/basket/withdraw';
import { UserBasket } from './mocked/basket/basket';
import { AddBasket } from './mocked/basket/add-basket';
import { UserBasketLibrary } from './mocked/basket/library';

@Injectable()
export class MockedInterceptor implements HttpInterceptor {
  constructor() {}

  protected baseUrl = 'http://45.156.184.158:8000';
  protected mockedClasses = [
    login,
    // logout,
    CourseList,
    AccountList,
    CourseCategoryList,
    BasketFinancial,
    BasketWithdraw,
    UserBasket,
    AddBasket,
    UserBasketLibrary
  ];

  intercept(
    request: HttpRequest<unknown>,
    next: HttpHandler
  ): Observable<HttpEvent<unknown>> {
    console.log('request', request);
    const apiClass = this.routeApi(request);
    if (apiClass) {
      // =>check auth need
      const res = this.checkAuthNeed(apiClass);
      if (res) return res;
      const response = new apiClass().handle(request);
      return response;
    }

    return next.handle(request);
  }

  checkAuthNeed(apiClass: typeof BaseMockedApi) {
    let accessToken = undefined;
    if (localStorage.getItem('token_VREDU')) {
      accessToken = localStorage.getItem('token_VREDU')!;
    }
    if (apiClass.isAuthCheck && accessToken !== MOCKED.manager_mocked_token) {
      return of(
        new HttpResponse({
          status: 401,
          body: { message: 'Access Denied' },
        })
      );
    }
    return undefined;
  }

  routeApi(request: HttpRequest<unknown>) {
    try {
      for (const api of this.mockedClasses) {
        if (api.method.toLowerCase() !== request.method.toLowerCase()) continue;
        const requestUrl = new URL(request.url);
        const requestCleanUrl = requestUrl.origin + requestUrl.pathname;
        let baseUrl = `${this.baseUrl}`;
        if (api.baseUrl) baseUrl = `${api.baseUrl}`;
        const res = new RegExp(`^${baseUrl}${api.path}$`).test(requestCleanUrl);
        if (!res) continue;
        return api;
      }
    } catch (e) {}
    return undefined;
  }
}
