import { AfterViewInit, Directive, HostListener, Input, OnInit } from '@angular/core';

@Directive({
  selector: '[Scroll]'
})
export class ScrollDirective implements AfterViewInit {

  @Input() elementRef: HTMLElement
  @Input() maxHeight
  tumbline: HTMLElement
  content: HTMLElement
  tumblinHeight
  constructor() { }
  ngAfterViewInit(): void {
    this.content = this.elementRef.querySelector('#content')
    this.content.style.transition = 'all 0.3s'
    this.tumblinHeight = this.content.offsetHeight
    if (this.tumblinHeight <= this.maxHeight) { return }
    const scroll = document.createElement('div')
    this.tumbline = document.createElement('div')
    this.tumbline.setAttribute('style', `
      width:7px;
      height:${(this.maxHeight / (this.tumblinHeight)) * 100}%;
       border-radius: 6px;
       box-shadow: inset 0 0 6px rgba(0,0,0,0.4); 
    `)
    scroll.appendChild(this.tumbline)
    scroll.setAttribute('style', `
       border-radius: 6px;
      width: 7px;
      position: absolute;
      z-index:999999;
      right: 0;
      height:${this.maxHeight}px;
      box-shadow: inset 0 0 6px rgba(85, 85, 85, 0.801); 
    `)
    this.elementRef.appendChild(scroll)

  }
  y = 0
  scroll = 0
  @HostListener('mousewheel', ['$event']) onMousewheel(event) {
    if (this.tumblinHeight <= this.maxHeight) {
      // if (event.wheelDelta < 0) {
      //   this.scroll -= 20
      // }
      // if (event.wheelDelta > 0) {
      //   this.scroll += 20
      // }
      // this.content.style.transform = 'translateY(' + this.scroll  + '%)'

      return
    }
    if (event.wheelDelta < 0) {
      if ((this.y + this.tumbline.offsetHeight) > this.maxHeight) { return }
      this.y += 10
      this.scroll -= ((this.maxHeight / this.tumbline.offsetHeight) / 10.068 * 100)
      console.log(this.scroll);

      this.tumbline.style.transform = 'translateY(' + this.y + 'px)'
      this.content.style.transform = 'translateY(' + this.scroll + 'px)'
    }
    if (event.wheelDelta > 0) {
      if (this.y === 0) { return }
      this.y -= 10
      this.scroll += ((this.maxHeight / this.tumbline.offsetHeight) / 10.068 * 100)
      this.tumbline.style.transform = 'translateY(' + this.y + 'px)'
      this.content.style.transform = 'translateY(' + this.scroll + 'px)'

    }

  }
  @HostListener('touchstart', ['$event'])
  onStart(event) {
    let touch = event.touches[0] || event.changedTouches[0];
    console.log('start', touch);
  }
  @HostListener('touchmove', ['$event']) onMove(event) {
    let touch = event.touches[0] || event.changedTouches[0];
    console.log('move', touch);
  }
  @HostListener('touchend', ['$event'])
  @HostListener('touchcancel', ['$event'])
  handleTouch(event) {
    let touch = event.touches[0] || event.changedTouches[0];
    console.log('end', touch);

  }
  // @HostListener('touchstart ', ['$event']) onStart(event: MouseEvent) {
  //   console.log(event);

  // }
  // @HostListener('touchend', ['$event']) onEnd(event: MouseEvent) {
  //   console.log(event);

  // }
  // @HostListener('touchmove', ['$event']) onMove(event: MouseEvent) {
  //   console.log(event);

  // }
}
