import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable, of, throwError} from 'rxjs';
import { catchError, retry } from 'rxjs/operators';
import {environment} from "../../../environments/environment";

export const API = `${environment.API_BASE}`;
const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json'
  }),
  // withCredentials: true
};
@Injectable({
  providedIn: 'root'
})
export class ShoppingService {

  constructor(private http:HttpClient) { }

  listBasket(){
    return this.http.get(`${API}/vredu/basket`)
      .pipe(catchError(handleError<any>('list basket')));
  }
  checkOut(){
    return this.http.get(`${API}/vredu/basket/checkout`)
      .pipe(catchError(handleError<any>('check out')));
  }
}
function handleError<T>(operation = 'operation', result?: T) {
  return (error: any): Observable<T> => {

    // TODO: send the error to remote logging infrastructure
    console.error(error); // log to console instead

    // TODO: better job of transforming error for user consumption
    // UtilitesService.log(`${operation} failed: ${error.message}`);

    return throwError(error.error);
  };
}
