import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { ServiceWorkerModule } from '@angular/service-worker';
import { APP_INITIALIZER } from '@angular/core';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { RouterModule } from '@angular/router';
import { MessageService } from './service/message.service';
import { MainModule } from './components/main.module';
import { ToastrModule } from 'ngx-toastr';

import {
  HttpClient,
  HttpClientModule,
  HTTP_INTERCEPTORS,
} from '@angular/common/http';
import { NgxUiLoaderConfig, NgxUiLoaderModule } from 'ngx-ui-loader';
import { HttpConfigInterceptor } from './auth/http-config.interceptor';
import { TranslateLoader, TranslateModule } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { AppInitService } from './service/app-init.service';
import { environment } from 'src/environments/environment';
import { MockedInterceptor } from './mocked.interceptor';
export function init_app(appLoadService: AppInitService) {
  return () => appLoadService.init();
}
export function HttpLoaderFactory(httpClient: HttpClient) {
  return new TranslateHttpLoader(
    httpClient,'/assets/i18n/', '.json'
  );
}
const loaderConfiguration: NgxUiLoaderConfig = {
  bgsColor: '#ffffff',
  bgsOpacity: 0.3,
  bgsPosition: 'bottom-right',
  bgsSize: 60,
  bgsType: 'chasing-dots',
  blur: 5,
  delay: 0,
  fastFadeOut: true,
  fgsColor: '#ffffff',
  fgsPosition: 'center-center',
  fgsSize: 60,
  fgsType: 'chasing-dots',
  gap: 24,
  logoPosition: 'center-center',
  logoSize: 170,
  // "logoUrl": "assets/angular.png",
  masterLoaderId: 'master',
  overlayBorderRadius: '0',
  overlayColor: 'rgba(0, 0,0,0.6)',
  pbColor: '#ffffff',
  pbDirection: 'ltr',
  pbThickness: 3,
  hasProgressBar: true,
  text: '',
  textColor: '#FFFFFF',
  textPosition: 'center-center',
  maxTime: -1,
  minTime: 300,
};

@NgModule({
  declarations: [AppComponent],
  imports: [
    BrowserAnimationsModule,
    BrowserModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: HttpLoaderFactory,
        deps: [HttpClient],
      },
      defaultLanguage: 'en',
    }),
    AppRoutingModule,
    MainModule,
    FormsModule,
    HttpClientModule,
    RouterModule,
    ServiceWorkerModule.register('ngsw-worker.js', {
      enabled: environment.production,
    }),
    ToastrModule.forRoot({
      positionClass: 'toast-bottom-right',
      preventDuplicates: true,
    }
  ),
    NgxUiLoaderModule.forRoot(loaderConfiguration),

  ],
  providers: [
    MessageService,
    AppInitService,
    {
      provide: APP_INITIALIZER,
      useFactory: init_app,
      deps: [AppInitService],
      multi: true,
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: HttpConfigInterceptor,
      multi: true,
    },
    { provide: HTTP_INTERCEPTORS, useClass: MockedInterceptor, multi: true }
  ],
  bootstrap: [AppComponent],
})
export class AppModule {}
