import { AuthService } from 'src/app/auth/auth.service';
import {
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  ViewChild,
} from '@angular/core';
import { MatMenuPanel } from '@angular/material/menu';
import { TranslateService } from '@ngx-translate/core';
import { Subscription } from 'rxjs';
import { GridDeviceConfig } from 'src/app/service/config-json';
import { MessageService } from 'src/app/service/message.service';
import { HeaderService } from './header.service';
import { ActivatedRoute, Router } from '@angular/router';
import { CoursesService } from 'src/app/pages/courses-page/components/courses.service';
import { ShoppingService } from 'src/app/pages/shopping/shopping.service';
import { environment } from '../../../environments/environment';
@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
})
export class HeaderComponent implements OnInit, OnDestroy {
  basketCounter: number = 0;
  basketItem: any[] = [];
  totalPrice: number = 0;
  private sideOpen: boolean = true;
  private sub: Subscription = new Subscription();
  search: boolean = false;
  body: any;
  fullScrean: boolean = false;
  @Input() dir = 'ltr';
  lang: string = 'English';
  setting: boolean = false;
  role = '';
  @ViewChild('tabs') tabs: ElementRef;
  @Output() toggle: EventEmitter<any> = new EventEmitter();
  constructor(
    private transletServis: TranslateService,
    private message: MessageService,
    private headerService: HeaderService,
    private auth: AuthService,
    private router: Router,
    public course: CoursesService,
    public service: ShoppingService
  ) {
    const SELF = this;
    window.onresize = function () {
      const deviseWidth = window.innerWidth;
      if (deviseWidth < 900) {
        SELF.sideOpen = false;
        SELF.message.sidenaveToggle.next(false);
      } else {
        SELF.sideOpen = true;
        SELF.message.sidenaveToggle.next(true);
      }
    };
    window.onload = function () {
      const deviseWidth = window.innerWidth;
      if (deviseWidth < 900) {
        SELF.sideOpen = false;
        SELF.message.sidenaveToggle.next(false);
      } else {
        SELF.sideOpen = true;
        SELF.message.sidenaveToggle.next(true);
      }
    };
  }

  get baseUrl() {
    return window.origin;
  }

  ngOnInit(): void {
    this.role = localStorage.getItem('role_vr');
    this.message.basket.subscribe((result) => {
      console.log(result);
      if (result) {
        this.basketCounter = result.length;
        this.basketItem = result;
        this.totalPrice = 0;
        for (var i = 0; i < result.length; i++) {
          this.totalPrice += result[i].price;
        }
      }
    });
    this.sub.add(
      this.message.sidenaveClose.subscribe((data) => {
        this.sideOpen = data;
      })
    );
    document.addEventListener('fullscreenchange', () => {
      if (document.exitFullscreen) {
        this.fullScrean = !this.fullScrean;
      }
    });
  }
  ngOnDestroy() {
    if (this.sub) this.sub.unsubscribe();
  }
  sideToggle() {
    this.sideOpen = !this.sideOpen;
    this.message.sidenaveToggle.next(this.sideOpen);
    this.toggle.emit(this.sideOpen);
  }
  fullScreen() {
    if (!this.fullScrean) {
      this.body = document.getElementById('body');
      if (this.body.requestFullscreen) {
        this.body.requestFullscreen();
      } else if (this.body.mozRequestFullScreen) {
        /* Firefox */
        this.body.mozRequestFullScreen();
      } else if (this.body.webkitRequestFullscreen) {
        /* Chrome, Safari and Opera */
        this.body.webkitRequestFullscreen();
      } else if (this.body.msRequestFullscreen) {
        /* IE/Edge */
        this.body.msRequestFullscreen();
      }
    } else {
      document.exitFullscreen();
    }
  }
  tabIndex = 0;
  tabChange(index) {
    this.tabIndex = index;
    this.dir = document.getElementById('body').getAttribute('dir');
    const width = this.tabs.nativeElement.offsetWidth;
    if (this.dir === 'ltr') {
      this.tabs.nativeElement.style.transform = `translateX(-${
        width * index
      }px)`;
    } else {
      this.tabs.nativeElement.style.transform = `translateX(${
        width * index
      }px)`;
    }
  }

  notificationToggle(menu: MatMenuPanel) {
    console.log(menu);
    document.getElementById(menu.panelId).style.minWidth = '366px';
  }

  toogleView(el: HTMLElement, className) {
    if (el.classList.contains(className)) {
      el.classList.remove(className);
    } else {
      el.classList.add(className);
    }

    // if()
  }
  changeLanguag(ln) {
    this.transletServis.use(ln);
    if (ln === 'en') {
      this.lang = 'English';
    } else {
      this.lang = 'فارسی';
    }
  }
  importConfig() {
    let upload: any = document.createElement('input');
    upload.setAttribute('type', 'file');
    upload.setAttribute('accept', 'application/json');
    // document.body.appendChild(upload)
    upload.click();
    upload.addEventListener('change', () => {
      const reader = new FileReader();
      reader.onload = (event) => {
        const config: GridDeviceConfig = JSON.parse(
          event.target.result.toString()
        );
        this.message.importConfigJson.next(config);
        // document.body.removeChild(upload)
      };
      if (upload.files[0].type === 'application/json') {
        reader.readAsText(upload.files[0]);
      } else {
        console.log('file type not suported');
      }
    });
  }
  /* Close fullscreen */
  //   closeFullscreen() {

  //     // else if (document.mozCancelFullScreen) { /* Firefox */
  //     //   document.mozCancelFullScreen();
  //     // } else if (document.webkitExitFullscreen) { /* Chrome, Safari and Opera */
  //     //   document.webkitExitFullscreen();
  //     // } else if (document.msExitFullscreen) { /* IE/Edge */
  //     //   document.msExitFullscreen();
  //     // }
  //   }

  logOut() {
    this.headerService.logOut().subscribe((result) => {
      this.auth.removeLocalStorage('token');
      this.router.navigate(['login']);
    });
  }
  deleteCourse(e, id) {
    e.stopPropagation();
    this.course.changeBasket('remove', id).subscribe((res) => {
      this.basket();
    });
  }
  basket() {
    this.service.listBasket().subscribe((res) => {
      let data = res?.basket;
      this.message.basketList = data ?? [];
      this.message.basket.next(data);
      this.basketItem = data;
      this.totalPrice = res?.sum;
    });
  }
}
