import { Injectable } from '@angular/core';
import { BehaviorSubject, Subject } from 'rxjs';
import { GridDeviceConfig } from './config-json';

@Injectable({
  providedIn: 'root'
})
export class MessageService {
  navPath: string = "";
  sidenaveToggle = new Subject<boolean>();
  sidenaveClose = new Subject<boolean>()
  sidenaveStatus = new BehaviorSubject<string>('full')
  dirChange = new BehaviorSubject<string>(null)
  loading: BehaviorSubject<{ loading: boolean, text: string, id: string }> = new BehaviorSubject<{ loading: boolean, text: string, id: string }>(null)
  basket: BehaviorSubject<any[]> = new BehaviorSubject<any[]>([])
  basketList = [];
  gridResize = new Subject<{ id: string, width: string, height: string }>()
  importConfigJson = new BehaviorSubject<GridDeviceConfig>(null);
  idOwner =new Subject<{ id: any }>()

  constructor() { }
}
